body.light-mode{
  background-color: rgb(232, 232, 232);
}

body.dark-mode{
  background-color: #1A202C;
}

.App {
  text-align: center;
}

.no-focus-out:focus,
.no-focus-out:active{
  outline: none;
  border: none;
  box-shadow: none !important;
}

a.anchor {
  display: block;
  position: relative;
  top: -250px;
  visibility: hidden;
}

.inside-text{
  position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 5;
    opacity: 0;
    text-align: center;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
}

.inside-text:hover{
  opacity: 1;
}

.inside-text:hover .inside-h3{
  -webkit-transform: translate(0, 0);
  -moz-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  -o-transform: translate(0, 0);
  transform: translate(0, 0);
}

.inside-text:hover .inside-btn{
  -webkit-transform: translate(0, 0);
  -moz-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  -o-transform: translate(0, 0);
  transform: translate(0, 0);
}

.inside-inside{
    position: absolute;
    top: 50%;
    width: 100%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.inside-h3{
  font-size: 1.4em;
  margin: 0;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 1em;
  position: relative;
  -webkit-transform: translate(0, -40px);
  -moz-transform: translate(0, -40px);
  -ms-transform: translate(0, -40px);
  -o-transform: translate(0, -40px);
  transform: translate(0, -40px);
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
}

.inside-btn{
    position: relative;
    -webkit-transform: translate(0, 40px);
    -moz-transform: translate(0, 40px);
    -ms-transform: translate(0, 40px);
    -o-transform: translate(0, 40px);
    transform: translate(0, 40px);
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    margin-top: 15px;
}

a:hover{
  color: transparent;
}

a{
  color: transparent;
}

a:focus,
button:focus{
  box-shadow: 0 0 0 3px rgba(233, 215, 51, 0.6) !important;
  outline: none;
}