.customBtn{
    margin: 20px;
    background-color: #8f7605;
    border-color: #8f7605 !important;
    color: #fff !important;
  }
  
  .customBtn:hover{
    color: #fff !important;
    background-color: #c7a719 !important;
    border-color: #c7a719 !important;
    box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba(255,180,0,.25) !important;
    font-size: 1.5rem;
  }
  
  .wallcat{
    background-image: url('../../Assets/wall2.png');
    background-size: cover;
    background-position: bottom center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;
  }
  
  .header{
    color: #fff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 3rem;
    text-align: center;
    margin: 10px 0;
    font-family: 'Righteous', sans-serif !important;
  }
  
  hr {
      height: 1px;
      width: 50%;
      border-top: 1px solid #ccc !important;
      margin: 1.125rem auto; 
      padding: 0;
  }
  
  .center{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .box{
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 40%;
  }
  
  @media only screen and (max-width: 767px) {
    .header {
      font-size: 2rem;
    }
  
    .quote{
      font-size: 0.7rem;
    }
  
    .box{
      width: 60%;
    }
  
    .wallcat{
      background-position: center center;
    }
  }